@import-normalize;

@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@300;400;500;600&display=swap');


* {
  box-sizing: border-box;
}

tr {
  border-width: 1px !important;
}

p, div {
  margin: 0 !important;  
}

body {
  background: black !important;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  display: flex;
  font-weight: 400 !important;
  font-family: 'Titillium Web', sans-serif !important;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  color: rgb(236, 236, 236);
}

h1 {
  font-size: 2.5rem !important;
}

label {
  font-size: 12px !important;
  margin-bottom: 0.3rem;
  vertical-align: middle;
}

body, html, .App, #root {
  width: 100%;
  height: 100%;
}



.navbar {
  padding: 3rem 0 !important;
  background: transparent!important;
}



.auth-inner {
  width: 550px;
  margin: auto;
  background: #161616;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #EB330F;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: white !important;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.nav-link {
  color: rgba(175, 175, 175, 0.7) !important;
  width: auto;
  padding: .5rem 0 !important;
}

.nav-item {
  text-align: center;
  display: inline;
  margin: 0 auto;
}






p.forgot-password.text-right {
  position: absolute;
  right: 0;
  display: inline-block;
}

form {
  position: relative;
}

.form-group:last-of-type {
  margin-bottom: 2rem !important;
}

.nav-link.selected::after {
  content: '';
  width: 100%;
  height: 1px;
  bottom: 0.2rem;
  left: 0;
  background: white;
  position: absolute;
}

.nav-link.selected {
  color: white !important;
}


.nav-link {
  position: relative;
}



.form-group {
  margin: 1rem 0;
}

input[type='checkbox'] {
  margin-right: .5rem;
}



.nav-list {
   display: flex;
   justify-content: flex-start !important;
   font-size: 1.2rem;
   margin-bottom: 2rem;

   div {
      margin-right: 1rem;
   }

 }
 

