@keyframes blinking {
  0% {
     background-color: #0078e7;
  }
  100% {
     background-color: #36304a;
     color: white;
  }
}
.updating {
  animation: blinking 1s infinite;
}


.rdt_TableHeadRow {
   background: #161616 !important;
   text-transform: uppercase;
}

ol, ul, li, p {
   list-style: none outside none;
   margin: 0;
   padding: 0;
}


// .dropdown:hover .dropdown-content {
